const module = {
  config: {
    name: 'RKAP-kualitatif',
    icon: 'ri-book-mark-line',
    desc: 'Digunakan untuk mengisi data RKAP Kualitatif Tahunan PT Hutama Karya',
    layout: true
  },
  routes: [
    { name: 'monitoring-pengumpulan', icon: 'ri-dashboard-line', path: '/monitoring-pengumpulan-kualitatif', component: () => import('./monitoring-kualitatif.vue') },
    { separator: 'Input Kualitatif', name: 'template-input-kualitatif', icon: 'ri-attachment-line', path: '/template-RKAP-kualitatif', component: () => import('./m-rkap-kualitatif.vue') },
    { separator: 'Input Kualitatif', name: 'pengumpulan-input-kualitatif', icon: 'ri-inbox-unarchive-line', path: '/RKAP-kualitatif', component: () => import('./rkap-kualitatif.vue') },
    { separator: 'Konfigurasi', label: 'Batas Pengumpulan Data', name: 'batas-kualitatif', icon: 'ri-calendar-event-line', path: '/config-kualitatif', component: () => import('./config-kualitatif.vue') }
  ]
}
export default module



